.living-example {
  position: relative;
  background-color: $background-gray;
  padding-top: 29px;
  padding-bottom: 25px;

  @media (min-width: $tablet-width) {
    background-color: $white;
    padding-top: 43px;
    padding-bottom: 5px;
    z-index: -2;

    &::after {
      content: "";
      width: 100vw;
      height: 462px;
      position: absolute;
      bottom: 0;
      background-color: $background-gray;
      z-index: -1;
    }
  }

  @media (min-width: $desktop-width) {
    position: relative;
    background-color: $basic_gray;
    padding-top: 0;
    z-index: 0;

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 194px;
      background-color: $white;
    }

    &::after {
      height: 462px;
      width: auto;
      left: 50%;
      right: 0;
      transform: translateX(320px);
      bottom: 0;
    }
  }
}

.living-example__inner {
  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.living-example__wrapper {
  @media (min-width: $desktop-width) {
    margin-right: 134px;
  }
}

.living-example__header {
  margin-bottom: 35px;
  color: $black;

  @media (min-width: $tablet-width) {
    margin-bottom: 64px;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    width: 100%;
    padding-top: 56px;
    padding-bottom: 76px;
    margin-bottom: 70px;
  }
}

.living-example__text {
  font-family: $arial;
  padding-right: 5px;
  margin: 0;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    padding-right: 6px;
    margin-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
    max-width: 455px;
    margin-bottom: 69px;
  }
}

.living-example__statistics {
  margin: 0;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin-bottom: 83px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.living-example__toggles {
  display: flex;
  justify-content: center;
}

.living-example__item {
  margin-left: -20px;
  margin-right: -20px;

  img {
    display: block;

    @media (min-width: $tablet-width) {
      width: 690px;
      height: 517px;
      margin-left: auto;
    }

    @media (min-width: $desktop-width) {
    }
  }

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}
